
.selectedPage {
  font-weight: 700;
}

.pageNum {
  width: 550px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
}

.pageNum span {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 0 5px;
  cursor: pointer;
  border: solid red 1px;
  width: 30px;
  height: 25px;
  border-radius: 5px;  
}

.pageNum button {
  cursor: pointer;
}
